









import Vue from 'vue';

export default Vue.extend({
  name: 'Counter',
  data() {
    return {
      countDownDate: new Date('Jan 22, 2022').getTime() as number,
      startDate: new Date('Apr 17, 2021').getTime() as number,
      now: new Date().getTime() as number,
    };
  },
  computed: {
    distance(): number {
      return this.countDownDate - this.now;
    },
    days(): number {
      return Math.floor(this.distance / (1000 * 60 * 60 * 24));
    },
    hours(): number {
      return Math.floor((this.distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
    },
    minutes(): number {
      return Math.floor((this.distance % (1000 * 60 * 60)) / (1000 * 60));
    },
    percentage(): number {
      return Math.floor(((this.countDownDate - this.now) * 100 ) / (this.countDownDate - this.startDate));
    },
  },
});
